import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { history } from '../app';
import { login } from '../store/actions/auth';

import * as displayActions from '../store/actions/display';

const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState();
  // const [isSignup, setIsSignup] = useState(false);

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(displayActions.setPageSidebar(false));
    dispatch(displayActions.setPageHeader(false));
    dispatch(displayActions.setPageFooter(false));
    dispatch(displayActions.setPageContentFullWidth(true));
    return () => {
      dispatch(displayActions.setPageSidebar(true));
      dispatch(displayActions.setPageHeader(true));
      dispatch(displayActions.setPageFooter(true));
      dispatch(displayActions.setPageContentFullWidth(false));
    };
  }, []);

  useEffect(() => {
    if (error) {
      console.log(error);
    }
  }, [error]);

  // const authHandler = async () => {
  //   let action;
  //   if (isSignup) {
  //     action = authActions.signup(
  //       formState.inputValues.email,
  //       formState.inputValues.password
  //     );
  //   } else {
  //     action = authActions.login(
  //       formState.inputValues.email,
  //       formState.inputValues.password
  //     );
  //   }
  //   setError(null);
  //   setIsLoading(true);
  //   try {
  //     await dispatch(action);
  //     // props.navigation.navigate('Shop');
  //   } catch (err) {
  //     setError(err.message);
  //     setIsLoading(false);
  //   }
  // };

  const submitHandler = async (e) => {
    e.preventDefault();

    try {
      await dispatch(login(email, password));
      history.push('/');
    } catch (err) {
      setError('Wrong email or password');
      // setIsLoading(false);
    }
  };

  return (
    <div className='login login-v1'>
      <div className='login-container'>
        <div className='login-header'>
          <Link to='/' style={{ textDecoration: 'none' }}>
            <div className='brand'>
              <span className='logo'></span>
              <b>Market</b> Profit
              <small>
                <Link to='/register'>
                  <button className='btn btn-success'>
                    <i className='icon-user-follow '> Регистрация</i>
                  </button>
                </Link>
              </small>
            </div>
          </Link>

          <div className='icon'>
            <i className='fa fa-lock'></i>
          </div>
        </div>
        <div className='login-body'>
          <div className='login-content'>
            <form className='margin-bottom-0' onSubmit={submitHandler}>
              <div className='form-group m-b-20'>
                <input
                  type='text'
                  className='form-control form-control-lg inverse-mode'
                  placeholder='Email Address'
                  value={email}
                  onChange={(e) => {
                    setEmail(e.target.value);
                  }}
                  required
                />
              </div>
              <div className='form-group m-b-20'>
                <input
                  type='password'
                  className='form-control form-control-lg inverse-mode'
                  placeholder='Password'
                  value={password}
                  onChange={(e) => {
                    setPassword(e.target.value);
                  }}
                  required
                />
              </div>
              <div className='checkbox checkbox-css m-b-20'>
                <input type='checkbox' id='remember_checkbox' />
                <label htmlFor='remember_checkbox'>Remember Me</label>
              </div>
              <div className='login-buttons'>
                <button
                  type='submit'
                  className='btn btn-success btn-block btn-lg'
                  onSubmit={submitHandler}
                >
                  Sign me in
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LoginPage;
