import React, { useState, useEffect } from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { createBrowserHistory } from 'history';

import Header from './components/header/header.jsx';
import Sidebar from './components/sidebar/sidebar.jsx';
import Footer from './components/footer/footer.jsx';
import MainPage from './pages/MainPage.js';
import NotFoundPage from './pages/NotFoundPage.js';
import LoginPage from './pages/LoginPage.js';
import RegistrationPage from './pages/RegistrationPage.js';
import BrandsPage from './pages/wildberries/BrandsPage.js';
import CatergoriesPage from './pages/wildberries/CategoriesPage.js';
import ProductsPage from './pages/wildberries/ProductsPage.js';
import HistoryPage from './pages/wildberries/HistoryPage.js';
import { fetchUser, login } from './store/actions/auth.js';
import UserPage from './pages/UserPage.js';

export const history = createBrowserHistory();

const App = () => {
  const [hasScroll, setHasScroll] = useState(false);
  const sidebar = useSelector((state) => !!state.dspl.pageSidebar);
  const header = useSelector((state) => !!state.dspl.pageHeader);
  const footer = useSelector((state) => !!state.dspl.pageFooter);
  const contentFullWidth = useSelector(
    (state) => !!state.dspl.pageContentFullWidth
  );
  const contentFullHeight = useSelector(
    (state) => !!state.dspl.pageContentFullHeight
  );

  const dispatch = useDispatch();

  // useEffect(() => {
  //   dispatch(fetchUser());
  // }, []);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleScroll = () => {
    if (window.scrollY > 0) {
      setHasScroll(true);
    } else {
      setHasScroll(false);
    }
    let elm = document.getElementsByClassName('nvtooltip');
    for (let i = 0; i < elm.length; i++) {
      elm[i].classList.add('d-none');
    }
  };

  return (
    <div
      className={`fade page-sidebar-fixed show page-container 
          ${header ? 'page-header-fixed ' : ''}
					${sidebar ? '' : 'page-without-sidebar '}
          ${contentFullWidth ? 'page-content-full-height ' : ''}
          ${contentFullHeight ? 'page-content-full-height ' : ''}
					${hasScroll ? 'has-scroll ' : ''}
				`}
    >
      <Router history={history}>
        <div>
          {header && <Header />}
          {sidebar && <Sidebar />}

          <Switch>
            <Route path='/' exact={true} component={MainPage} />
            <Route path='/login' component={LoginPage} />
            <Route path='/register' component={RegistrationPage} />
            <Route path='/user' component={UserPage} />
            <Route path='/wildberries/brands' component={BrandsPage} />
            <Route path='/wildberries/categories' component={CatergoriesPage} />
            <Route path='/wildberries/products' component={ProductsPage} />
            <Route path='/wildberries/history' component={HistoryPage} />

            <Route exact component={NotFoundPage} />
          </Switch>
          {footer && <Footer />}
        </div>
      </Router>
    </div>
  );
};

export default App;
