import { FETCH_USER, LOGIN, LOGOUT, SIGNIN, SIGNUP } from '../actions/auth';

const initialState = {
  token: null,
  isLogin: false,

  user: {
    id: null,
  },
};

export default (state = initialState, action) => {
  switch (action.type) {
    case LOGIN:
      return {
        ...state,
        isLogin: action.payload.success,
        user: { ...state.user, id: action.payload.id },
      };
    case LOGOUT:
      return {
        ...initialState,
      };

    case SIGNUP:
      return {
        ...state,
        isLogin: action.success,
        user: { id: action.payload.id },
      };

    case FETCH_USER:
      return {
        ...state,
        isLogin: action.success,
        user: { ...state.user, ...action.payload.user },
      };
    default:
      return state;
  }
};
